import {request} from './request'

//获得订舱单列表
export async function GetAllBookingList(data){
    return await request({
        method:'POST',
        url:'/api/BusinessUnit/GetAllBookingList',
        data:data
    })
}
//新增订舱单
//更新状态
export async function UpdateStateBookingList(data){
    return await request({
        method:'PUT',
        url:'/api/BusinessUnit/UpdateStateBookingList',
        data:data
    })
}
//获得单个订舱单信息
export async function GetSingleBookingList(data){
    return await request({
        url:'/api/BusinessUnit/GetSingleBookingList',
        params:{input:data}
    })
}
//更新订舱单信息
export async function UpdateBookingList(data){
    return await request({
        method:'PUT',
        url:'/api/BusinessUnit/UpdateBookingList',
        data:data
    })
}
//批量删除订舱单
export async function DeleteBookingList(data){
    return await request({
        method:'DELETE',
        url:'/api/BusinessUnit/DeleteBookingList',
        data:data
    })
}
//批量删除详情订舱单
export async function DeleteBookingDetailList(data){
    return await request({
        method:'DELETE',
        url:'/api/BusinessUnit/DeleteBookingDetailList',
        data:data
    })
}
