export class UpdateState{
    /* value——选中的数据
       state——下拉选中的状态 */
    constructor(value,state){
     let arr = new Array;
     value.forEach((item,index) => {
         arr[index] = item.id;
     });
     this.ids = arr;
     this.state = state;
    }
}

