<template>
  <div id="summary">
    <el-row class="text-right">
      <el-button type="primary" @click="insert">添加</el-button>
      <el-button type="danger" @click="remove">删除</el-button>
    </el-row>
    <!--  -->
    <el-col
      style="
        line-height: 40px;
        border: 1px;
        border-style: solid solid none solid;
      "
      >订舱单</el-col
    >
    <!-- 列表区 -->
    <el-col
      style="
        border: 1px;
        border-style: solid none none solid;
        line-height: 40px;
      "
      v-for="item in headerItems"
      :key="item.index"
    >
      <el-col :span="8" v-for="subitem in item" :key="subitem.index">
        <el-col :span="10">{{ subitem.label }}</el-col
        ><el-col
          :span="14"
          style="border: 1px; border-style: none solid none solid"
          ><el-input v-model="formData[subitem.value]"></el-input
        ></el-col>
      </el-col>
    </el-col>
    <!-- 表格区 -->
    <el-table border id="middle" @select="rowclick" :data="formData.tableData">
      <el-table-column type="selection" width="50"></el-table-column>
      <el-table-column type="index" label="序号" width="50"></el-table-column>
      <el-table-column
        v-for="item in tableList"
        :label="item.label"
        :width="item.width"
        :key="item.index"
      >
        <template slot-scope="scope">
          <el-input v-model="scope.row[item.prop]"></el-input>
        </template>
      </el-table-column>
    </el-table>
    <el-row class="text-left"
      ><div>备注</div>
      <div style="display: inline-block; min-width: 90%">
        <el-input type="textarea" v-model="mark"></el-input></div
    ></el-row>
    <!-- 提交区 -->
    <el-row class="text-right" 
      ><el-button @click="submit">提交</el-button>
      <el-button @click="cancel">取消</el-button></el-row
    >
  </div>
</template>

<script>
import { GetSingleBookingList, DeleteBookingDetailList,UpdateBookingList} from "network/orderStore.js";
import { arraySort } from "service/dateUtil.js";

import { TableItem } from "service/Business/orderStore.js";
export default {
  data() {
    return {
      spanArr: [],
      client: "22",
      headerItems: [
        [
          { label: "客户", value: "customerName" },
          { label: "港口", value: "shippingAddress" },
          { label: "制单时间", value: "creationTime" },
        ],
        [
          { label: "客户公司地址", value: "customerCompany" },
          { label: "进仓日期", value: "warehousingDate" },
          { label: "业务员", value: "salesManName" },
        ],
        [
          { label: "客户联系电话", value: "customerPhone" },
          { label: "贸易方式", value: "tradeMode" },
          { label: "审核表", value: "reviewerName" },
        ],
      ],
      mark:'',
      formData: { 
        tableData: [],
        creationTime:'',
        customerCompany:'',
        customerName:'',
        customerPhone:'',
        reviewerName:'',
        salesManName:'',
        shippingAddress:'',
        tradeMode:'',
        warehousingDate:''
      },
      tableList: [
        { label: "合同号", prop: "contractNo", width: "150" },
        { label: "订单号", prop: "orderNo", width: "150" },
        { label: "PO号", prop: "poNo", width: "150" },
        { label: "货号", prop: "itemNo", width: "150" },
        { label: "客人款号", prop: "guestNo", width: "150" },
        { label: "箱子品", prop: "caseNo", width: "150" },
        { label: "品名", prop: "productName", width: "150" },
        { label: "CPID", prop: "cpid", width: "150" },
        { label: "尺寸", prop: "size", width: "150" },
        { label: "配比", prop: "ratio", width: "150" },
        { label: "条形码", prop: "barCode", width: "150" },
        { label: "单价", prop: "unitPrice", width: "150" },
        { label: "商品品质表示", prop: "trademarkQualityMark", width: "150" },
        { label: "双/组", prop: "doubleGroup", width: "150" },
        { label: "组/箱", prop: "groupBox", width: "150" },
        { label: "出货PK数", prop: "shipmentPKNumber", width: "150" },
        { label: "出货数(双)", prop: "shipmentNumber", width: "150" },
        { label: "箱数", prop: "boxNumber", width: "150" },
        { label: "长(M)", prop: "length", width: "150" },
        { label: "宽(M)", prop: "width", width: "150" },
        { label: "高(M)", prop: "height", width: "150" },
        { label: "N.W/箱（kg）", prop: "nwBox", width: "150" },
        { label: "G.W/箱（kg）", prop: "gwBox", width: "150" },
        { label: "总体积（M3)", prop: "volume", width: "150" },
        { label: "总金额", prop: "totalAmount", width: "150" },
        { label: "总净重（kg）", prop: "netWeight", width: "150" },
        { label: "总毛重（kg）", prop: "grossWeight", width: "150" },
        { label: "备注", prop: "note", width: "150" },
      ],
      items: ["sample", "index", "PO", "client"],
      emptyTableItem: [],
      selectRow: -1,
      selectData: [],
      deleteData:[],
      isContainBlank: false,
    };
  },
  created() {
    this.inital();
  },
  methods: {
    //初始化
    inital() {
      //判断新建还是编辑
      //新建
      if (this.$route.query.iid === undefined) {
      }
      //编辑
      else {
        var data = this.$route.query.iid;
        GetSingleBookingList(data).then((res) => {
          this.formData = res.result;
          this.formData.tableData = res.result.detail;
          this.addIndex(this.formData.tableData);
        });
      }
    },
    //数据编号
    addIndex(data){
      data.forEach((item,index)=>{
        item.index = index;
      })
    },
    getDate() {
      Date.prototype.format = function () {
        var o = {
          y: this.getFullYear(),
          mo: this.getMonth() + 1,
          d: this.getDate(),
          h: this.getHours(),
          mi: this.getMinutes(),
        };
        return "" + o.y + "/" + o.mo + "/" + o.d + "/" + o.h + ":" + o.mi;
      };
      var date = new Date();
      return date.format();
    },
    setdate() {
      var i = this.getDate();
    },
    /* 筛选得到数据 */
    filter(row) {
      var obj = new Object();
      for (var i = 0; i < this.items.length; i++) {
        if (i < 3) {
          obj[this.items[i]] = row[this.items[i]];
        } else {
          obj[this.items[i]] = "";
        }
      }
      console.log(obj);
      return obj;
    },
    /* 获得选中行数据 */
    rowclick(selection, row){
      this.selectData = selection;
    },
    // 插入一行 
    insert() {
      const tableItem = new TableItem();
      if(this.selectData.length==0){
        this.formData.tableData.push(tableItem);
      }
      else{
        let index = this.selectData[0].index;
        this.formData.tableData.splice((index+1),0,tableItem);
      }
      this.addIndex(this.formData.tableData);
    },
    //删除按钮
    remove() {
      const data = this.selectData;
      //判断是否为空
      if (data.length == 0) {
        this.$message.error("请先选择删除项！");
      } else {
        //arr存放传给后台删除id，arr1存放界面删除的索引
        let arr = new Array(data.length);
        let arr1 = new Array(data.length);
        data.forEach((item, index) => {
          arr[index] = item.id;
          arr1[index] = item.index;
        });
        //排序
        arr1 = arraySort(arr1);
        //对界面数据进行删除
        arr1.forEach((item) => {
          this.formData.tableData.splice(item, 1);
        });
        this.deleteData = arr;
        this.addIndex(this.tableData);
      }
    },
    //删除操作
    delete() {
      DeleteBookingDetailList(this.deleteData).then((res) => {
        if (res.success) {
          this.$message.success("删除成功");
        }
      });
    },
    //校验
    validation() {
      try {
        this.formData.tableData.forEach((item, index) => {
          for (var i in item) {
            if (item[i] === "") {
              this.$message.error("第" + (index + 1) + "行含有空字符串");
              this.isContainBlank = true;
              throw new Error("End Loop");
            } else {
              this.isContainBlank = false;
            }
          }
        });
      } catch (e) {
        //catch语句跳出forEach循环
      }
    },
    //提交操作
    submit() {
      //校验有没有空数据
      this.validation();
      if (!this.isContainBlank) {
        this.delete();
        UpdateBookingList(this.formData).then((res) => {
          if (res.success) {
            this.$router.push("/orderStore");
          }
        });
      }
    },
    //取消
    cancel() {
      this.$router.go(-1);
    },
   
  },
};
</script>

<style scoped>
.el-input {
  width: 65% !important;
}
.el-row {
  margin-bottom: 10px;
  line-height: 40px;
}
.text-left {
  border: solid;
  border-width: 0 1px 1px 1px;
  height: 56px;
}
.text-left > div:first-child {
  display: inline-block;
  width: 51px;
  height: 100%;
  border-right: 1px solid;
  line-height: 56px;
}
</style>